.food-display{
    margin: 30px auto;
    width: 90%;
}

.food-display .top{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.food-display h2{
    font-size: max(2vw,24px);
    font-weight: 600;
}

.food-display-list{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(240px,1fr));
    gap: 30px;
    margin-top: 30px;
    row-gap: 50px;
}