.food-item{
    width: 100%;
    margin: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    transition: 0.3s;
    animation: fadeIn 1s;
}

.food-item-img{
    width: 100%;
    border-radius: 15px 15px 0px 0px;
}

.food-item-info{
    padding: 20px;
}

.food-item-name-rating{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.food-item-name-rating p{
font-size: 20px;
font-weight: 600;
}

.food-item-name-rating img{
    width: 75px;
}

.food-item-desc{
    color: #676767;
    font-size: 12px;
}

.food-item-price{
    color: tomato;
    font-size: 22px;
    font-weight: 500;
    margin: 10px 0px;
}

.food-item-img-container{
    position: relative;
}

.food-item-img-container .add{
  position: absolute;
  width: 35px;
  bottom: 15px;
  right: 15px;
  border-radius: 50%;
  cursor: pointer;
}

.food-item-counter{
    cursor: pointer;
    position: absolute;
    bottom: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: white;
    border-radius: 50px;
    padding: 6px;
}

.food-item-counter img{
    width: 30px;
}